import React from 'react';
import styled from 'styled-components';
import * as urls from 'components/common/imgUrls/indexImgs';

const DetailsStyled = styled.ul`
  width: 100%;
  
  li {
    align-items: center;
    border-bottom: 1px solid ${props => props.colors.grayLight};
    display: none;
    height: 22.5rem;
    justify-content: flex-end;
    width: 100%;

    h4 {
      color: ${props => props.colors.greenDark};
      font-size: 26px;
      padding-bottom: 1rem;
      width: 100%;
    }

    p {
      margin-bottom: 5rem;
      width: 16rem;
      word-break: keep-all;
    }

    &.active {
      display: flex;
    }
    
    &:nth-of-type(1){
      background: url(${urls.trainer}) left top/499px no-repeat;
    }

    &:nth-of-type(2){
      background: url(${urls.insurance}) left top/499px no-repeat;
    }

    &:nth-of-type(3){
      background: url(${urls.safetyZone}) left top/499px no-repeat;
    }

    &:nth-of-type(4){
      background: url(${urls.liveTracking}) left top/499px no-repeat;
    }
  }

`;

const detailsList = [
  {
    id: 1,
    class: "active",
    title: "   ",
    descript: "     ,    . ( )",
  },
  {
    id: 2,
    class: "",
    title: "  ",
    descript: "         .",
  },
  {
    id: 3,
    class: "",
    title: " ",
    descript: "      .      ",
  },
  {
    id: 4,
    class: "",
    title: " ",
    descript: "         .",
  },
];

const IntroWfDetails = ({ colors }) => {
  return (
    <DetailsStyled colors={colors}>
      {detailsList.map(v => {
        return(
          <li
            key={v.id}
            className={v.class}
          >
            <h4>{v.title}</h4>
            <p>
              {v.descript}
            </p>
          </li>
        )
      })}
    </DetailsStyled>
  );
};

export default IntroWfDetails;